<template>
  <div>
    <d2-container>
      <template slot="header"></template>
      <br>
      <!---->
      <table-comb
        name=""
        ref="tableMain"
        :search-model-base="tableMainSearchModelBase"
        :get-action="$api.tet.stationCorp.stationHistory"
        :get-action-where="getActionWhere"
        custom-id="stationId"
        dispaly-name="stationName"
        :autoFetch="false"
      >
        <!---->
        <template slot="baseSearchForm" slot-scope="props">
          <el-select
            v-model="props.form.siteId"
            @change="changeSite"
            filterable
            placeholder=""
            style="width: 300px;">
            <el-option
              v-for="item in siteOptions"
              :key="item.siteId"
              :label="item.name"
              :value="item.siteId">
            </el-option>
          </el-select>
          <el-input
            placeholder=""
            prefix-icon="el-icon-search"
            clearable
            v-model="props.form.searchKey"
            style="width: 160px; margin-left: 10px;"
          >
          </el-input>
          <el-button class="fr" @click="openDialog()"></el-button>
        </template>
        <!---->
        <template slot="tableColumns">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column fixed="left" label="">
            <template slot-scope="props">
              <el-button type="text" size="mini" @click="openDialog(props.row)"></el-button>
              <el-button type="text" size="mini" @click="showHistory(props.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="officeName" label=""></el-table-column>
          <el-table-column prop="levelName" label=""></el-table-column>
          <el-table-column prop="stationName" label=""></el-table-column>
          <el-table-column prop="corpName" label=""></el-table-column>
          <el-table-column prop="telephone" label="">
            <template slot-scope="props">
              {{(props.row.corpContacts||'--')+'/'+(props.row.telephone||'--')}}
            </template>
          </el-table-column>
        </template>
      </table-comb>
      <template slot="footer"></template>
    </d2-container>

    <el-dialog :title="editDialogCustomer.checkedItems.length>1 ?'':''" :visible.sync="showSaveDialog">
      <el-form label-width="120px" :model="editDialogCustomer" :rules="rules" ref="saveForm">
        <template v-if="editDialogCustomer.checkedItems.length==1">
          <el-form-item label="">
            {{editDialogCustomer.checkedItems[0].officeName}}
          </el-form-item>
          <el-form-item label="">
            {{editDialogCustomer.checkedItems[0].levelName}}
          </el-form-item>
          <el-form-item label="">
            {{editDialogCustomer.checkedItems[0].stationName}}
          </el-form-item>
        </template>
        <template v-else>
          <el-form-item label="">
            <span style="color:#409eff"></span>
          </el-form-item>
        </template>
        <el-form-item label="" prop="corpId">
          <el-select
            v-model="editDialogCustomer.corpName"
            @change="changeCorp"
            placeholder=""
            filterable
            remote
            :remote-method="getCorpList"
            :loading="corpLoading"
            class="w400"
          >
            <el-option
              v-for="item in corpList"
              :key="item.id"
              :label="item.name"
              :value="item.name">
              {{item.name}}
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <span>{{editDialogCustomer.corpContacts||'--'}}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{editDialogCustomer.telephone||'--'}}</span>
        </el-form-item>
        <el-form-item label="" prop="settleStartTime">
          <el-date-picker
            v-model="editDialogCustomer.settleStartTime"
            type="date"
            :picker-options="settleStartTimeOptions"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="editDialogCustomer.settleEndTime"
            type="date"
            :picker-options="settleEndTimeOptions"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save"> </el-button>
        <el-button @click="showSaveDialog = false"> </el-button>
      </div>
    </el-dialog>

    <corp-history ref="historyDialog" @fetchData="handleUpdateSuccess"></corp-history>
  </div>
</template>

<script>
  import listMixin from "@/mixins/list.mixin";
  import CorpHistory from "../componnets/CorpHistory"

  export default {
    // 
    // name  name 
    name: "FeeAudit",
    components: {CorpHistory},
    mixins: [
      listMixin
    ],
    data() {
      return {
        tableMainSearchModelBase:{
          siteId:'',
          searchKey:''
        },
        corpList:[],
        siteOptions:[],
        tableData: [],
        showSaveDialog:false,
        //vo
        editDialogCustomer:{
          checkedItems:[],
          corpId:'',
          corpName:'',
          telephone:'',
          settleStartTime:'',
          settleEndTime:'',
        },
        corpLoading: false,
        //
        settleStartTimeOptions:{
          disabledDate: (time) => {
            let settleEndTime = this.editDialogCustomer.settleEndTime;
            if (settleEndTime) {
              return time.getTime() > settleEndTime;
            }
          }
        },
        //
        settleEndTimeOptions:{
          disabledDate: (time) => {
            let settleStartTime = this.editDialogCustomer.settleStartTime;
            if (settleStartTime) {
              return time.getTime() < settleStartTime;
            }
          }
        },
        rules:{
          corpId: [
            {required: true, message: '', trigger: 'change' }
          ],
          settleStartTime:[
            {required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    computed: {
      getActionWhere(){
        return {
          unionId : this.unionId
        }
      },
      unionId() {
        return this.$route.params.unionId
      },
    },
    methods: {
      //
      openDialog(item){
        console.log(item);
        let checkedItems = [];
        //item item
        if(item){
          checkedItems = [item];
        }else{
          //
          checkedItems =this.$refs.tableMain.checkedItems;
        }
        console.log(checkedItems);
        this.editDialogCustomer = {
          checkedItems,
          corpId:'',
          corpName:'',
          telephone:'',
          settleStartTime:'',
          settleEndTime:'',
        }
        //
        if(this.editDialogCustomer.checkedItems.length==0){
          this.$message.error('');
          return;
        }

        if(this.editDialogCustomer.checkedItems.length==1){
          let _item =  this.editDialogCustomer.checkedItems[0];
          this.editDialogCustomer.corpName = _item.corpName;
          this.editDialogCustomer.corpId = _item.corpId;
          this.editDialogCustomer.corpContacts = _item.corpContacts;
          this.editDialogCustomer.telephone = _item.telephone;
        }
        this.editDialogCustomer.stationIds =this.editDialogCustomer.checkedItems.map(item=>{
          return item.stationId
        });
        this.showSaveDialog = true;
        //
        this.$refs.saveForm && this.$refs.saveForm.clearValidate();
      },
      //
      handleUpdateSuccess() {
        console.log('')
        this.$refs.tableMain.fetchData();
      },
      //
      getCorpList(searchKey) {
        let par ={
          pageSize:50,
          unionId:this.unionId
        };
        if (searchKey !== '') {
          par.searchKey = searchKey;
        }
        this.$api.corp.list(par).then(res => {
          this.corpList = res.data.data;
        })
      },
      //
      save(){
        //
        this.$refs.saveForm.validate((valid) => {
          if (valid) {
            let vo = {
              corpId:this.editDialogCustomer.corpId,
              stationIds:this.editDialogCustomer.stationIds,
              settleStartTime:this.editDialogCustomer.settleStartTime,
              settleEndTime:this.editDialogCustomer.settleEndTime
            }
            this.$api.tet.stationCorp.saveStationCorp(vo).then(res => {
              if(res.code==0){
                let message ='';
                this.showSaveDialog = false;
                this.$message({
                  message,
                  type: 'success'
                });
                //
                this.$refs.tableMain.fetchData()
              }else{
                this.$message.error(res.message);
              }
            });
          } else {
            return false;
          }
        });
      },
      //
      showHistory(item){
        this.$refs.historyDialog.open({...item,unionId:this.unionId});
      },
      //
      changeSite(siteId){
        //
        this.$refs.tableMain.fetchData();
      },
      changeCorp(name){
        //id
        let corp = this.corpList.filter(item=>{
          return item.name = name;
        })[0];
        this.editDialogCustomer.corpId =corp.id;
        this.editDialogCustomer.corpContacts =corp.userName;
        this.editDialogCustomer.telephone =corp.telephone;
      },
    },
    mounted() {
      //
      setTimeout(()=>{
        this.$api.merch.region.siteOptions(this.unionId).then(res => {
          let siteId = res.data[0].siteId
          this.$refs.tableMain.searchModelDataBase.siteId = siteId
          this.siteOptions = res.data;
          this.changeSite(siteId);
        });

      },0)
    }
  };
</script>

<style type="text/scss" lang="scss" scoped>
  .el-tag{ margin-right: 10px; cursor: pointer;}
  .el-dialog__body{ padding: 0 20px;}
  .w400{ width:400px;}
</style>
