<template>
  <div>
    <d2-container>
      <template slot="header"></template>
      <br>
      <!---->
      <table-comb
        name=""
        ref="tableMain"
        :search-model-base="tableMainSearchModelBase"
        :get-action="$api.tet.feeType.getFeeTypeList"
        :get-action-where="getActionWhere"
        :remove-action="$api.tet.feeType.deleteFeeType"
      >
        <!---->
        <template slot="baseSearchForm" slot-scope="props">
          <el-input placeholder="" prefix-icon="el-icon-search" clearable v-model="props.form.description" style="width: 160px; margin-left: 10px;"></el-input>
          <el-button class="fr" @click="openFeeTypeDialog()"></el-button>
        </template>
        <!---->
        <template slot="tableColumns">
          <el-table-column prop="corpName" label="" width="100px">
            <template slot-scope="props" >
              <template v-if="props.row.source!='0'">
                <el-button type="text" size="mini" @click="openFeeTypeDialog(props.row)"></el-button>
                <el-button type="text" size="mini" @click="deleteFeeType(props.row)"></el-button>
              </template>
              <template v-else ></template>
            </template>
          </el-table-column>
          <el-table-column prop="description" label=""></el-table-column>
          <el-table-column prop="sourceName" label=""></el-table-column>
        </template>
      </table-comb>
      <template slot="footer"></template>
    </d2-container>

    <el-dialog :title="editDialogCustomer.id ?'':''" :visible.sync="showSaveDialog">
      <el-form label-width="100px" :model="editDialogCustomer" :rules="rules" ref="saveForm">
        <el-form-item label=""  prop="description">
          <el-input v-model="editDialogCustomer.description" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveFeeType"> </el-button>
        <el-button @click="showSaveDialog = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import listMixin from "@/mixins/list.mixin";

  export default {
    // 
    // name  name 
    name: "FeeType",
    components: {},
    mixins: [
      listMixin
    ],
    data() {
      return {
        tableMainSearchModelBase:{
          corpName: ''
        },
        tableData: [],
        showSaveDialog:false,
        editDialogCustomer:{
          description:'',
          source:1,
          type:2,
          id:''
        },
        rules:{
          description: [
            { required: true, message: '', trigger: 'blur' }
          ],
        }
      };
    },
    computed: {
      getActionWhere(){
        return {
          unionId : this.unionId
        }
      },
      unionId() {
        return this.$route.params.unionId
      },
    },
    methods: {
      //
      openFeeTypeDialog(item){
        this.showSaveDialog = true;
        //
        this.$refs.saveForm && this.$refs.saveForm.clearValidate();
        //item item
        this.editDialogCustomer = item||{description:'',source:1,type:2,};
      },
      //
      saveFeeType(){
        //
        console.log(this.editDialogCustomer);
        this.$refs.saveForm.validate((valid) => {
          console.log(valid)
          if (valid) {
            this.$api.tet.feeType.saveFeeType(this.editDialogCustomer).then(res => {
              if(res.code==0){
                let message = this.editDialogCustomer.id?'':''
                this.showSaveDialog = false;
                this.$message({
                  message,
                  type: 'success'
                });
                //
                this.$refs.tableMain.fetchData()
              }else{
                this.$message.error(res.message);
              }
            });
          } else {
            return false;
          }
        });
      },
      //
      deleteFeeType(item){
        this.$refs.tableMain.remove(item.id);
      }
    },
    mounted() {
      const _this = this;
    }
  };
</script>

<style type="text/scss" lang="scss" scoped>
  .el-tag{ margin-right: 10px; cursor: pointer;}
  .el-dialog__body{ padding: 0 20px;}
</style>
