<template>
  <transition name="edit">
    <d2-container v-show="isVisible" v-loading="loading" class="edit-panel">
      <template slot="header">
        <i title="" class="el-icon-back" @click="isVisible = false"></i>
        
      </template>

      <div class="customer-detail" v-if="formData">
        <div class="base-info">
          <div class="title">{{formData.corpInfoVO.corpName}}</div>
          <div class="tags">
            <el-row :gutter="24">
              <el-col :span="18">
                <el-tag>{{formData.corpInfoVO.industryDesc}}</el-tag>
                <el-tag>{{isActiveService ? '' : ''}}</el-tag>
                <el-tag v-for="labelName in formData.corpLabelNameList">{{labelName}}</el-tag>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" @click="openEditLabelDialog "></el-button>
              </el-col>
            </el-row>

          </div>
        </div>
        <el-tabs v-model="activeTabName" type="card" class="details-info">
          <el-tab-pane label="" name="first">
            <el-form label-width="140px">
              <div class="group">
                <div class="group-title"></div>
                <el-row>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.corpInfoVO.corpName}}</el-form-item>
                  </el-col>
                  <!--<el-col :span="8">-->
                  <el-form-item label="">{{formData.corpInfoVO.scale}}</el-form-item>
                  <!--</el-col>-->
                  <el-col :span="8">
                    <el-form-item label="">{{formData.corpInfoVO.industryDesc}}</el-form-item>
                  </el-col>
                </el-row>
              </div>
              <div class="group">
                <div class="group-title"></div>
                <el-row>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.manageVO.userName}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.manageVO.displayName}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.manageVO.telephone}}</el-form-item>
                  </el-col>
                </el-row>
              </div>
              <div class="group">
                <div class="group-title"></div>
                <el-row>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.unionInfo.createType}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.unionInfo.unionName}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.unionInfo.regSite}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.unionInfo.createTsStr}}</el-form-item>
                  </el-col>
                  <!--<el-col :span="8">-->
                  <!--<el-form-item label="">{{formData.unionInfo.siteLabel}}</el-form-item>-->
                  <!--</el-col>-->
                </el-row>
              </div>
              <div class="group">
                <div class="group-title">
                  <el-switch v-model="isActiveService" disabled></el-switch>
                  <el-alert title="" type="info" :closable="false"></el-alert>
                </div>
                <br>
                <el-row>
                  <el-col :span="24">
                    <el-tag type="info" v-for="(item, index) in openServices" :key="index">{{item}}</el-tag>
                  </el-col>
                </el-row>
                <br>
              </div>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-form label-width="140px">
              <div class="group">
                <div class="group-title"></div>
                <el-row>
                  <el-col :span="8">
                    <el-tag type="success" v-if="formData.certStatusVO.certStatus === '1'"></el-tag>
                    <el-tag type="info" v-else></el-tag>
                  </el-col>
                </el-row>
                <br>
              </div>
              <div class="group">
                <div class="group-title"></div>
                <el-row>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.unifiedSocialCreditCode ? '' : ''}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.unifiedSocialCreditCode}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label=""><a href="javascript:void(0);" @click="viewPic(formData.businessInfoVO.license15FullFileUrl)"></a></el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.juridicalPerson}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.juridicalPersonId}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label=""><a href="javascript:void(0);" @click="viewPic(formData.businessInfoVO.license04FullFileUrl)"></a></el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.corpTypeStr}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.typeCodeStr}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.registerAuthority}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.registerMoney}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.foundTime | dateFormat}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.registerMoney | dateFormat}} ~ {{formData.businessInfoVO.businessEndtime | dateFormat}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.businessInfoVO.registerAddress}}</el-form-item>
                  </el-col>
                </el-row>
              </div>
              <div class="group">
                <div class="group-title"></div>
                <el-row>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.tickerOpeningVO.title}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.tickerOpeningVO.identificationNumber}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.tickerOpeningVO.contactNumber}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.tickerOpeningVO.corpAddress}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.tickerOpeningVO.openingBank}}</el-form-item>
                  </el-col>
                  <el-col :span="8">
                    <el-form-item label="">{{formData.tickerOpeningVO.bankNo}}</el-form-item>
                  </el-col>
                </el-row>
              </div>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
      <el-dialog :append-to-body="true" :visible.sync="dialogPicVisible">
        <img :src="viewPicUrl" v-if="viewPicUrl" style="width: 100%;"/>
      </el-dialog>
      <el-dialog title="" :visible.sync="showLabelDialog" v-if="formData" :append-to-body="true">
        <el-form label-width="100px" :model="editDialogCustomer">
          <h2>{{formData.corpInfoVO.corpName}}</h2>
          <el-form-item label="">
            <el-checkbox v-for="(label,index) in editDialogCustomer.labels" v-if="label.labelType==='0'" :label="label.labelName" :key="index"  v-model="label.checkFlag"></el-checkbox>
          </el-form-item>
          <el-form-item label="">
            <el-tag v-for="(label,index) in editDialogCustomer.labels" :key="index" @click.native="likeCheckbox(label)" v-if="label.labelType==='1'" :type="label.checkFlag?'danger':'info'">{{label.labelName}}</el-tag>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="submitLabels"> </el-button>
          <el-button @click="showLabelDialog = false"> </el-button>
        </div>
      </el-dialog>
    </d2-container>
  </transition>

</template>

<script>
  import formMixin from '@/mixins/form.mixin';

  export default {
    name: "CorpDetail",
    components: {},
    mixins: [
      formMixin
    ],
    data() {
      return {
        isVisible: false,
        activeTabName: "first",
        formData: null,
        dialogPicVisible: false,
        viewPicUrl: '',
        showLabelDialog:false,
        editDialogCustomer:{
            labels:[]
        }
      };
    },
    computed: {
      isActiveService() {
        return this.formData.openService.certStatus === '1'
      },
      openServices() {
        return this.formData.openService.openServices;
        // return this.formData.openService && this.formData.openService.openServices ? this.formData.openService.openServices.split(',') : [];
      }
    },
    methods: {
      open(customer) {
        let _this = this;
        this.$api.corp.detail(customer.id).then(res => {
          _this.formData = res.data;
          _this.formData.siteId = customer.siteId;
          _this.formData.unionId = customer.unionId;
          _this.isVisible = true;
          _this.loading = false;
        });
      },
      viewPic(url) {
        this.viewPicUrl = url;
        this.$nextTick(() => {
          this.dialogPicVisible = true;
        })
      },
      //
      openEditLabelDialog(){
          const _this = this;
          this.showLabelDialog=true;
          this.$api.corp.getCorpLabelList(_this.formData.corpInfoVO.corpId,_this.formData.siteId).then(res => {
              _this.editDialogCustomer.labels = res.data;
          });
      },
      //
      likeCheckbox(label){
          label.checkFlag = !label.checkFlag;
      },
      //
      submitLabels(){
        const _this = this;
        const labelId = [];
        const labelName=[];
        this.editDialogCustomer.labels.map(function(item){
          if(item.checkFlag){
            labelId.push(item.id);
            labelName.push(item.labelName)
          }

        });
        this.$api.corp.saveCorpLabelList(_this.formData.corpInfoVO.corpId,labelId.join(','),this.formData.unionId,_this.formData.siteId).then(res => {
          if(res.code==0){
            _this.showLabelDialog = false;
            _this.$message({
              message: '',
              type: 'success'
            });
            _this.formData.corpLabelNameList = labelName;
          }else{
            _this.$message.error(res.message);
          }
        });
      }
    }
  };
</script>

<style type="text/scss" lang="scss" scoped>
  .customer-detail {
    .base-info {
      margin-bottom: 20px;
      .title {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 15px;
      }
      .tags {
        span {
          margin-right: 10px;
        }
      }
    }
    .details-info {
      .group {
        margin-bottom: 10px;
        border-bottom: solid 1px #eee;
        .group-title {
          line-height: 3;
          font-size: 14px;
          font-weight: bold;
          .el-alert {
            font-size: 12px;
            line-height: 1.5;
          }
        }
      }
    }
  }

  .el-tag {
    margin-right: 10px;
    cursor: pointer;
  }

  .el-dialog__body {
    padding: 0 20px;
  }
</style>
