<template>
  <div>
    <d2-container>
      <template slot="header"></template>
      <br>
      <!---->
      <table-comb
        name=""
        ref="tableMain"
        :search-model-base="tableMainSearchModelBase"
        :search-model="tableMainSearchModel"
        :get-action="$api.energy.meterUnitRel.list"
        :get-action-where="getActionWhere"
        :autoFetch="false"
      >
        <!---->
        <template slot="baseSearchForm" slot-scope="props">
          <el-select
            v-model="props.form.siteId"
            @change="changeSite"
            filterable
            placeholder=""
            style="width: 300px;">
            <el-option
              v-for="item in siteOptions"
              :key="item.siteId"
              :label="item.name"
              :value="item.siteId">
            </el-option>
          </el-select>
          <el-input
            placeholder="/"
            prefix-icon="el-icon-search"
            clearable
            v-model="props.form.searchKey"
            style="width: 210px; margin-left: 10px;"
          >
          </el-input>
          <el-button class="fr" @click="exportExcel">EXCEL</el-button>
        </template>
        <!---->
        <template slot="advancedSearchForm" slot-scope="props">
          <el-row>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.officeId"
                  @change="changeOffice"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in officeOptions"
                    :key="item.id"
                    :label="item.description"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-cascader
                  placeholder=""
                  :props="regionProps"
                  :options="regionOptions"
                  v-model="props.form.regionId"
                  :change-on-select="true"
                  :debounce="400"
                  @change="changeRegion">
                </el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.stationId"
                  @change="changeStation"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in stationOptions"
                    :key="item.id"
                    :label="item.staDescription"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.isPooled"
                  @change="changeStation"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in pooledOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.deviceStatus"
                  @change="changeStation"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in deviceStatuses"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.runStatus"
                  @change="changeStation"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in runStatuses"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </template>
        <!---->
        <template slot="tableColumns">
          <el-table-column prop="corpName" label="">
            <template slot-scope="props">
              <el-button type="text" size="mini" @click="openDialog(props.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="deviceNum" label=""></el-table-column>
          <el-table-column prop="displayName" label=""></el-table-column>
          <el-table-column prop="deviceStatus" label=""></el-table-column>
          <el-table-column prop="isPooled" label=""></el-table-column>
          <el-table-column prop="office" label=""></el-table-column>
          <el-table-column prop="levelName" label="">
            <template slot-scope="props">
              {{(props.row.levelName||'-')+'/'+(props.row.level2Name||'-')}}
            </template>
          </el-table-column>
          <el-table-column prop="station" label=""></el-table-column>
        </template>
      </table-comb>
      <template slot="footer"></template>
    </d2-container>

    <el-dialog title="" :visible.sync="showSaveDialog">
      <el-form
        label-width="100px"
        :model="editDialogCustomer"
        :rules="rules"
        ref="saveForm">
        <el-form-item label="" >
          {{editDialogCustomer.deviceNum}}
        </el-form-item>
        <el-form-item label=""  prop="displayName">
          <el-input
            placeholder=""
            style="width: 214px"
            clearable
            v-model="editDialogCustomer.displayName"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="editDialogCustomer.officeId"
            @change="changeOfficeForSave"
            placeholder=""
            filterable
          >
            <el-option
              v-for="item in editDialogCustomer.officeOptions"
              :key="item.id"
              :label="item.description"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            placeholder=""
            :props="regionProps"
            :change-on-select="true"
            :debounce="400"
            :options="editDialogCustomer.regionOptions"
            v-model="editDialogCustomer.regionId"
            @change="changeRegionForSave">
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="stationId">
          <el-select
            v-model="editDialogCustomer.stationId"
            placeholder=""
            filterable
          >
            <el-option
              v-for="item in editDialogCustomer.stationOptions"
              :key="item.id"
              :label="item.staDescription"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save"> </el-button>
        <el-button @click="showSaveDialog = false"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  import listMixin from "@/mixins/list.mixin";

  export default {
    // 
    // name  name 
    name: "MeterUnitRel",
    components: {},
    mixins: [
      listMixin
    ],
    data() {
      //
      let changeStation = (rule, value, callback)=>{
        if(this.editDialogCustomer.officeId && value ===''){
          callback(new Error(''));
        }
        callback();
      };
      return {
        tableMainSearchModelBase:{
          corpName: ''
        },
        tableMainSearchModel:{
          officeId:'',
          regionId:[],
          stationId:'',

        },
        siteOptions:[],
        officeOptions:[],
        regionOptions:[],
        regionProps:{
          value: 'id',
          label:'name',
          children: 'childLists'
        },
        stationOptions:[],
        //
        pooledOptions:[
          {value: '', label: ''},
          {value: '0', label: ''},
          {value: '1', label: ''}
        ],
        //
        deviceStatuses:[
          {value: '0', label: ''},
          {value: '1', label: ''},
          {value: '2', label: ''},
          {value: '3', label: ''},
          {value: '4', label: ''},
        ],
        //
        runStatuses:[
          {value: '0', label: ''},
          {value: '1', label: ''}
        ],
        tableData: [],
        showSaveDialog:false,
        editDialogCustomer:{
          officeId:'',
          regionId:[],
          stationId:'',
          id:'',
          officeOptions:[],
          regionOptions:[],
          stationOptions:[],
        },
        rules:{
          displayName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          stationId: [
            {validator: changeStation,trigger: 'blur'  }
          ],
        }
      };
    },
    computed: {
      getActionWhere(){
        return {
          unionId : this.unionId
        }
      },
      unionId() {
        return this.$route.params.unionId
      },
    },
    methods: {
      //
      openDialog(item){
        console.log(item);
        this.showSaveDialog = true;
        //
        this.$refs.saveForm && this.$refs.saveForm.clearValidate();
        //item item
        this.editDialogCustomer = {
          ...item,
          regionId:[item.levelId||'',item.level2Id||''],
          officeOptions:[],
          regionOptions:[],
          stationOptions:[],
        }
        //
        this.initEditDialogCustomerOptions()

      },
      initEditDialogCustomerOptions(){
        let siteId = this.$refs.tableMain.searchModelDataBase.siteId;
        let officeId = this.editDialogCustomer.officeId;
        let regionId = this.editDialogCustomer.regionId;
        let stationId = this.editDialogCustomer.stationId;
        this.$api.merch.region.officeOptions(siteId).then(res => {
          this.editDialogCustomer.officeOptions = res.data;
        });
        if(!officeId){
          return
        }
        this.$api.merch.station.regionOptions(officeId).then(res => {
          this.editDialogCustomer.regionOptions = res.data;
        });
        if(!regionId[0]){
          return
        }
        this.$api.merch.station.stationOptions(regionId,officeId).then(res => {
          this.editDialogCustomer.stationOptions = res.data;
        });
      },
      //value  lable
      getLabelByValue(list,value){
        console.log(value)
        let arr =  list.filter(item=>{
          return item.value == value;
        });
        if(!value || arr.length==0){
          return ''
        };
        return arr[0].label;
      },
      //
      save(){
        //
        console.log(this.editDialogCustomer);
        this.$refs.saveForm.validate((valid) => {
          console.log(valid)
          if (valid) {
            let vo = {
              ...this.editDialogCustomer
            };

            delete vo.officeOptions;
            delete vo.regionOptions;
            delete vo.stationOptions;

            vo.levelId = vo.regionId[0];
            vo.level2Id = vo.regionId[1];
            delete vo.regionId;

            this.$api.energy.meterUnitRel.save(vo).then(res => {
              if(res.code==0){
                let message = ''
                this.showSaveDialog = false;
                this.$message({
                  message,
                  type: 'success'
                });
                //
                this.$refs.tableMain.fetchData()
              }else{
                this.$message.error(res.message);
              }
            });
          } else {
            return false;
          }
        });
      },
      //
      exportExcel(){
        if(this.$refs.tableMain.tableData.length==0){
          this.$message.error('');
          return;
        }
        let vo = this.$refs.tableMain.getPageVo();
        console.log(vo);
        this.$api.energy.meterUnitRel.exportExcel(vo)
      },
      //
      changeSite(siteId){
        this.$api.merch.region.officeOptions(siteId).then(res => {
          this.officeOptions =  this.editDialogCustomer.officeOptions  = res.data;
          // 
          let searchModelData = this.$refs.tableMain.searchModelData;
          searchModelData.officeId='';
          searchModelData.regionId=[];
          searchModelData.stationId='';
          this.regionOptions=[];
          this.stationOptions=[];
          //
          this.$refs.tableMain.fetchData();

        });
      },
      //
      changeOffice(officeId){
        this.$api.merch.station.regionOptions(officeId).then(res => {
          this.regionOptions = res.data;
          // 
          let searchModelData = this.$refs.tableMain.searchModelData;
          searchModelData.regionId=[];
          searchModelData.stationId='';
          this.stationOptions=[];
          //
          this.$refs.tableMain.fetchData();

        });
      },
      //
      changeRegion(regionIdArray){
        let officeId = this.$refs.tableMain.searchModelData.officeId
        this.$api.merch.station.stationOptions(regionIdArray,officeId).then(res => {
          this.stationOptions = res.data;
          // 
          this.$refs.tableMain.searchModelData.stationId='';
          //
          this.$refs.tableMain.fetchData();
        });
      },
      //
      changeStation(stationId){
        this.$refs.tableMain.fetchData();
      },

      //
      changeOfficeForSave(officeId,callback){
        this.$api.merch.station.regionOptions(officeId).then(res => {
          let editDialogCustomer = this.editDialogCustomer;
          editDialogCustomer.regionOptions = res.data;
          // 
          editDialogCustomer.regionId=[];
          editDialogCustomer.stationId='';
          editDialogCustomer.stationOptions=[];
          if(callback){
            callback()
          }
        });
      },
      changeRegionForSave(regionIdArray){
        let officeId = this.editDialogCustomer.officeId
        this.$api.merch.station.stationOptions(regionIdArray,officeId).then(res => {
          let editDialogCustomer = this.editDialogCustomer;
          editDialogCustomer.stationOptions = res.data;
          // 
          editDialogCustomer.stationId='';
        });
      },
      // end
    },
    mounted() {
      //
      setTimeout(()=>{
        this.$api.merch.region.siteOptions(this.unionId).then(res => {
          let siteId = res.data[0].siteId
          this.$refs.tableMain && (this.$refs.tableMain.searchModelDataBase.siteId = siteId)
          this.siteOptions = res.data;
          this.changeSite(siteId);
        });
      },0)
    }
  };
</script>

<style type="text/scss" lang="scss" scoped>
  .el-tag{ margin-right: 10px; cursor: pointer;}
  .el-dialog__body{ padding: 0 20px;}
</style>
