// 
//  routerConfig
const routerConfigMenuOut = [
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: '/401',
    name: 'Unauthorized',
    component: Error401,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: '*',
    component: Error404,
  },
];

// 
//  router.js 

export default UtilIce.recursiveRouterConfig([
  ...routerConfig,
  ...routerConfigMenuOut,
]);

// 
//  mian.js 

export const frameInRoutes = UtilIce.recursiveRouterConfig(routerConfig).map(
  e => {
    const route = e.children ? e.children[0] : e;
    return {
      path: e.path,
      name: route.name,
      meta: route.meta,
    };
  }
);
