<template>
  <div>
    <d2-container>
      <template slot="header"></template>
      <br>
      <!---->
      <table-comb
        name=""
        ref="tableMain"
        :autoFetch="false"
        :search-model-base="tableMainSearchModelBase"
        :get-action="$api.energy.ecaSystemSiteRel.list"
        :get-action-where="getActionWhere"
        :remove-action="$api.energy.ecaSystemSiteRel.delete"
      >
        <!---->
        <template slot="baseSearchForm" slot-scope="props">
          <el-select
            v-model="props.form.siteId"
            filterable
            placeholder=""
            @change="changeSite"
            style="width: 300px;">
            <el-option
              v-for="item in siteOptions"
              :key="item.siteId"
              :label="item.name"
              :value="item.siteId">
            </el-option>
          </el-select>
          <el-input
            placeholder=""
            prefix-icon="el-icon-search"
            clearable
            v-model="props.form.searchKey"
            style="width: 160px; margin-left: 10px;">
          </el-input>
          <el-button type="primary" icon="el-icon-plus" class="fr" @click="openDialog()"></el-button>
        </template>
        <!---->
        <template slot="tableColumns">
          <el-table-column fixed="left" label="">
            <template slot-scope="props">
              <el-button type="text" size="mini" @click="syncDeviceList(props.row)"></el-button>
              <el-button type="text" size="mini" @click="openDialog(props.row)"></el-button>
              <el-button type="text" size="mini" @click="deleteItem(props.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column
            prop="mfrsName"
            label="">
          </el-table-column>
          <el-table-column
            prop="parkSystemName"
            label="">
          </el-table-column>
          <el-table-column
            prop="total"
            label="">
          </el-table-column>
        </template>
      </table-comb>

      <template slot="footer"></template>
    </d2-container>

    <el-dialog :title="editDialogCustomer.id ?'':''" :visible.sync="showSaveDialog">
      <el-form label-width="100px" :model="editDialogCustomer" :rules="rules" ref="saveForm">
        <el-form-item v-if="editDialogCustomer.mfrsName" label="" prop="mfrsName">
          {{editDialogCustomer.mfrsName}}
        </el-form-item>
        <el-form-item v-else label="" prop="systemId">
          <el-select v-model="editDialogCustomer.systemId" placeholder="">
            <el-option
              v-for="item in mfrsOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="parkSystemName">
          <el-input placeholder="" v-model="editDialogCustomer.parkSystemName" ></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="url">
          <el-input placeholder="" v-model="editDialogCustomer.url" ></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="gatewayUrl">
          <el-input placeholder="" v-model="editDialogCustomer.gatewayUrl" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save"> </el-button>
        <el-button @click="showSaveDialog = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import listMixin from "@/mixins/list.mixin";
  export default {
    // 
    // name  name 
    name: "EnergyList",
    components: {},
    mixins: [
      listMixin
    ],
    data() {
      return {
        tableMainSearchModelBase:{
          searchKey: '',
          siteId: '',
        },
        tableData: [],
        siteOptions: [],
        mfrsOptions:[],
        showSaveDialog:false,
        editDialogCustomer:{
        },
        defaultVo:{
          id:'',
          systemId:'',
          parkSystemName:'',
          url:'',
          gatewayUrl:'',
        },
        rules:{
          systemId: [
            { required: true, message: '', trigger: ['blur', 'change'] }
          ],
          parkSystemName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          url: [
            { required: true, message: 'URL', trigger: 'blur' }
          ],
          gatewayUrl: [
            { required: true, message: 'URL', trigger: 'blur' }
          ],
        }
      };
    },
    computed: {
      getActionWhere(){
        return {
          //unionId : this.unionId
        }
      },
      unionId() {
        return this.$route.params.unionId
      },
    },
    methods: {
      // 
      openDialog(item) {
        this.showSaveDialog = true;
        //
        if(item){
          this.editDialogCustomer= {...item};
        }else{
          this.editDialogCustomer =this.defaultVo;
        }
        this.$refs.saveForm && this.$refs.saveForm.clearValidate();

      },
      //
      deleteItem(item){
        this.$refs.tableMain.remove(item.id);
      },
      //
      changeSite(siteId){
          //
          this.$refs.tableMain.fetchData();
      },
      //
      save(){
        this.$refs.saveForm.validate((valid) => {
          if (valid) {
            this.editDialogCustomer.siteId = this.$refs.tableMain.searchModelDataBase.siteId;
            this.$api.energy.ecaSystemSiteRel.save(this.editDialogCustomer).then(res => {
              if(res.code==0){
                let message = this.editDialogCustomer.id ?'':'';
                this.showSaveDialog = false;
                this.$message({
                  message,
                  type: 'success'
                });
                //
                this.$refs.tableMain.fetchData()
              }else{
                this.$message.error(res.message);
              }
            });
          } else {
            return false;
          }
        });
      },
      //
      syncDeviceList(item){
        this.$api.energy.ecaSystemSiteRel.syncDeviceList(item.id).then(res => {
          if(res.code==0){
            let message = '';
            this.$message({
              message,
              type: 'success'
            });
            //
            this.$refs.tableMain.fetchData()
          }else{
            this.$message.error(res.message);
          }
        });
      }

    },
    mounted() {
      //
      setTimeout(()=>{
        this.$api.merch.region.siteOptions(this.unionId).then(res => {
          let siteId = res.data[0].siteId
          this.$refs.tableMain.searchModelDataBase.siteId = siteId
          this.siteOptions = res.data;
          this.changeSite(siteId);
        });
        this.$api.energy.ecaSystemSiteRel.getEcaSystemList().then(res => {
          this.mfrsOptions = res.data;
        });

      },0)
    }
  };
</script>

<style type="text/scss" lang="scss" scoped>
  .el-tag{ margin-right: 10px; cursor: pointer;}
  .el-dialog__body{ padding: 0 20px;}
</style>
