    <d2-container>
      <template slot="header"></template>
      <el-row>
        <el-col :span="12">
          <div class="grid-content-top">
            <p class="key"></p>
            <p class="value">{{totalData.auditStatusWaitNum||0}}</p>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="grid-content-top">
            <p class="key"></p>
            <p class="value">{{totalData.auditStatusWaitAmt||0}}</p>
          </div>
        </el-col>
      </el-row>
      <!---->
      <table-comb
        name=""
        ref="tableMain"
        :search-model-base="tableMainSearchModelBase"
        :search-model="tableMainSearchModel"
        :get-action="$api.energy.billManage.list"
        :get-action-where="getActionWhere"
        :autoFetch="false"
        :remove-action="$api.energy.billManage.delete"
        dispaly-name="billNo"
      >
        <!---->
        <template slot="baseSearchForm" slot-scope="props">
          <el-select
            v-model="props.form.siteId"
            @change="changeSite"
            filterable
            placeholder=""
            style="width: 300px;">
            <el-option
              v-for="item in siteOptions"
              :key="item.siteId"
              :label="item.name"
              :value="item.siteId">
            </el-option>
          </el-select>
          <el-input
            placeholder=""
            prefix-icon="el-icon-search"
            clearable
            v-model="props.form.searchKey"
            style="width: 240px; margin-left: 10px;"
          >
          </el-input>
          <el-button type="info" class="fr ml10" @click="auditBill('02')"></el-button>
          <el-button type="success" class="fr" @click="auditBill('01')"></el-button>
        </template>
        <!---->
        <template slot="advancedSearchForm" slot-scope="props">
          <el-row>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.officeId"
                  @change="changeOffice"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in officeOptions"
                    :key="item.id"
                    :label="item.description"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-cascader
                  placeholder=""
                  :props="regionProps"
                  :options="regionOptions"
                  v-model="props.form.regionId"
                  :debounce="400"
                  @change="changeRegion">
                </el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.stationId"
                  @change="changeStation"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in stationOptions"
                    :key="item.id"
                    :label="item.staDescription"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="props.form.auditStatus"
                  @change="changeStation"
                  placeholder=""
                  filterable
                >
                  <el-option
                    v-for="item in auditStatusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-date-picker
                    v-model="props.form.billMonth"
                    type="month"
                    format="yyyy  MM "
                    value-format="yyyy-MM"
                    placeholder="">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
        </template>
        <!---->
        <template slot="tableColumns">
          <el-table-column type="selection" width="35"></el-table-column>
          <el-table-column prop="corpName" label="" width="90px">
            <template slot-scope="props">
              <el-button type="text" size="mini" @click="detail(props.row)"></el-button>
              <el-button v-if="props.row.auditStatus==='00'" type="text" size="mini" @click="openSaveDialog(props.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column width="150px" prop="billNo" label=""></el-table-column>
          <el-table-column width="150px" prop="billSource" label=""></el-table-column>
          <el-table-column prop="auditStatusDesc " label=""></el-table-column>
          <el-table-column width="150px" prop="officeName" label=""></el-table-column>
          <el-table-column prop="levelDesc" label=""></el-table-column>
          <el-table-column prop="stationName" label=""></el-table-column>
          <el-table-column width="120px" prop="billMonthStr" label=""></el-table-column>
          <el-table-column prop="feeTypeDesc" label=""></el-table-column>
          <el-table-column prop="receivableAmtStr" label=""></el-table-column>
          <el-table-column width="120px" prop="receivableDateStr" label=""></el-table-column>
          <el-table-column prop="corpName" label=""></el-table-column>
          <el-table-column prop="telephone" label="">
            <template slot-scope="props">
              {{(props.row.corpContacts||'-')+'/'+(props.row.telephone||'-')}}
            </template>
          </el-table-column>
        </template>
      </table-comb>
      <template slot="footer"></template>
    </d2-container>

