            <el-form-item label="" prop="officeId">
              <el-select
                      v-model="editDialogCustomer.officeId"
                      @change="changeOfficeForSave"
                      placeholder=""
                      filterable
              >
                <el-option
                        v-for="item in editDialogCustomer.officeOptions"
                        :key="item.id"
                        :label="item.description"
                        :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :disabled="corpDisabled" v-model="editDialogCustomer.corpName"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="regionId">
              <el-cascader
                      placeholder=""
                      :props="regionProps"
                      :options="editDialogCustomer.regionOptions"
