                        :key="item.id"
                        :label="item.staDescription"
                        :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" >
              <el-input :disabled="corpDisabled" v-model="editDialogCustomer.telephone" ></el-input>
