                <el-col :span="12">
                  <el-form-item label="">
                    <el-input v-model="editDialogCustomer.peakUseQuantity" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <el-input v-model="editDialogCustomer.peakBeginMeterReading" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.peakPrice" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.peakEndMeterReading" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.peakRate" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="" name="third">
              <el-row>
                <el-col :span="12">
                  <el-form-item label="">
                    <el-input v-model="editDialogCustomer.plainUseQuantity" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <el-input v-model="editDialogCustomer.plainBeginMeterReading" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.plainPrice" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.plainEndMeterReading" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
