                <el-col :span="12">
                  <el-form-item label="">
                    <el-input v-model="editDialogCustomer.valleyUseQuantity" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <el-input v-model="editDialogCustomer.valleyBeginMeterReading" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.valleyPrice" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.valleyEndMeterReading" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    <el-input v-model="editDialogCustomer.valleyRate" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-tab-pane>
