              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    {{currentBill.tipPrice }}
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" >
                    {{currentBill.tipEndMeterReading }}
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" >
                    {{currentBill.tipRate }}
                  </el-form-item>
                </el-col>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <el-row>
